.App {
  display: flex;
  margin: 20px auto;
  flex-direction: column;
  align-items: center;
  
  width: 500px;
}

.button {
  padding: 10px 15px;
  margin: 3px;

  border: 0;
  border-radius: 15px;

  transition: box-shadow 400ms;

  &:hover {
    box-shadow: 1px 5px 5px silver;
  }
}
